






















import Vue from 'vue';
import { Component, Watch } from 'vue-property-decorator';
import { componentMenu } from '@/data';

@Component
export default class extends Vue {
  componentMenu: any[] = componentMenu;
  routeName:string = '';

  @Watch('$route')
  getRoute() {
    this.routeName = this.$route.name;
  }

  created() {
    this.getRoute();
  }

  switchNav(name) {
    this.$router.push({ name: name });
  }
}



